.btn {
  height: 48px;
  border-radius: 25px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: -0.1px;
  padding: 0 32px;
  border: none;
  white-space: nowrap;
  &_primary {
    background-color: $sunkissed-orange;
    color: $perfect-white;
  }
  &_secondary {
    background-color: $grey-dawn-w2;
    color: $perfect-white;
  }
  &_w_arrow {
    &::after {
      content: '';
      display: inline-block;
      background-image: url(data:image/svg+xml;base64,);
      width: 10px;
      height: 15px;
      margin-left: 8px;
      margin-right: -16px;
    }
  }
}

.link {
  color: $sunkissed-orange;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: -.1px;
  text-decoration: none;
  &::after {
    content: '';
    display: inline-block;
    width: 10px;
    height: 15px;
    margin-left: 8px;
    margin-right: -16px;
    background-image: url(data:image/svg+xml;base64,);
  }
  &.disabled {
    &::after {
      display: none;
    }
  }
  &_white {
    color: $perfect-white;
    &::after {
      background-image: url(data:image/svg+xml;base64,);
    }
  }
}
