.time {
  margin-top: 120px;
  &__inner {
    @include row-flex();
    flex-direction: column;
    @include laptop {
      align-items: center;
      flex-direction: row;
    }
  }
  &__img {
    @include col();
    @include size-lg(4);
    @include shift-lg-left(1);
    text-align: center;
    margin-bottom: 68px;
    img {
      display: inline-block;
      width: 168px;
      margin: auto;
      @include tablet {
        width: 421px;
      }
      @include laptop {
        width: 100%;
      }
    }
  }
  &__desc {
    @include col();
    @include tablet {
      @include u-size(6, 8, 24px);
    }
    @include size-lg(5);
    @include shift-lg-left(1);
    text-align: center;
    @include tablet {
      text-align: left;
    }
    p {
      margin-bottom: 36px;
    }
  }
}
