.h1 {
  font-weight: bold;
  font-size: rem(24);
  line-height: rem(36);
  letter-spacing: .8px;
  color: $light-text;
  text-align: center;
  margin-bottom: 16px;
  @include tablet {
    font-size: rem(32);
    line-height: rem(40);
    font-weight: normal;
    text-align: left;
  }
  @include laptop {
    font-size: rem(48);
    line-height: rem(56);
    letter-spacing: -0.04px;
    font-weight: 300;
  }
}

p {
  font-size: rem(12);
  line-height: rem(20);
  color: rgba($text-color, .72);
  text-align: center;
  @include tablet {
    text-align: left;
  }
  @include laptop {
    font-size: rem(16);
    line-height: rem(24);
  }
  &.lg {
    font-size: rem(20);
    line-height: rem(32);
  }
  &.md {
    font-size: rem(14);
    line-height: rem(24);
    letter-spacing: .28px;
  }
}

.h2 {
  font-size: rem(32);
  line-height: rem(40);
  font-weight: bold;
  letter-spacing: -.04px;
  margin-bottom: rem(16);
  text-align: center;
  color: $light-text;
  @include tablet {
    text-align: left;
  }
}

.h4 {
  font-size: rem(24);
  font-weight: bold;
  line-height: rem(36);
  letter-spacing: .8px;
  color: $light-text;
}

.h6 {
  font-weight: 500;
  color: $light-text;
  line-height: rem(24);
  letter-spacing: .12px;
  margin-bottom: 8px;
  text-align: center;
  @include tablet {
    text-align: left;
  }
}
