.footer {
  &__inner {
    border-top: 1px solid rgba($perfect-white, 0.1);
    padding-top: 43px;
    padding-bottom: 25px;
    text-align: center;
  }
  &__copyright {
    margin: 10px 0 15px;
    font-weight: 500;
    line-height: rem(24);
    color: #5C4B74;
  }
  &__menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 572px;
    margin: 0 auto;
    a {
      color: $perfect-white;
      font-weight: 500;
      line-height: rem(24);
      margin: 0 8px 12px;
      text-decoration: none;
    }
  }
}
