.menu {
  display: none;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(5px);
  background-image: radial-gradient(circle at 100% 100%, #572577, #4b3aa6);
  padding-top: 85px;
  padding-left: 29px;
  flex-direction: column;
  transition: opacity .15s ease-in-out, visibility .15s ease-in-out;
  &.open {
    opacity: 1;
    visibility: visible;
  }
  @include laptop {
    display: flex!important;
    flex-direction: row;
    margin: 0 25px;
    opacity: 1;
    visibility: visible;
    position: static;
    width: auto;
    height: auto;
    background-image: none;
    backdrop-filter: none;
    padding-top: 0;
    padding-left: 0;
  }
  & > li {
    &:not(:last-child) {
      margin-right: 30px;
    }
    & > a {
      color: rgba($perfect-white, .48);
      text-decoration: none;
      transition: color .15s ease-in-out;
      position: relative;
      display: block;
      font-weight: 500;
      font-size: 34px;
      line-height: 2;
      letter-spacing: .8px;
      &:hover,
      &.active {
        color: $perfect-white;
      }
      @include laptop {
        line-height: 24px;
        font-size: 16px;
        letter-spacing: normal;
        &.active {
          &::before {
            display: block;
          }
        }
      }
      &::before {
        content: '';
        position: absolute;
        top: -20px;
        left: 50%;
        transform: translateX(-50%);
        width: 28px;
        height: 4px;
        border-radius: 2px;
        background-color: $sunkissed-orange;
        display: none;
      }
    }
  }
}
