.social {
  margin-bottom: 120px;
  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @include tablet {
      flex-wrap: nowrap;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  &__link {
    height: 66px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;
    color: #7F6D99;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    padding: 0 7px;
    transition: .25s ease-in-out;
    svg {
      margin: auto;
      fill: #3e2b59;
      transition: .25s ease-in-out;
    }
    span {
      display: none;
      @include tablet {
        display: block;
      }
    }
  }
}
