.header {
  padding: 0 24px;
  max-width: 1440px;
  margin: 0 auto;
  position: relative;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 72px;
    @include laptop {
      height: 92px;
    }
  }
  .logo {
    position: relative;
    z-index: 1;
    width: 180px;
    flex-shrink: 0;
    img {
      max-width: 100%;
      display: block;
    }
    @include laptop {
      width: auto;
    }
  }
  .login {
    display: none;
    margin-right: 15px;
    @include laptop {
      display: block;
    }
  }
}
.menu-btn {
  width: 51px;
  height: 41px;
  background-color: rgba($perfect-white, .1);
  border-radius: 6px;
  text-align: center;
  padding-top: 2px;
  margin-left: auto;
  position: relative;
  z-index: 1;
  flex-shrink: 0;
  border: none;
  &.active {
    span {
      background-color: $perfect-white;
    }
  }
  @include laptop {
    display: none;
  }
  span {
    display: inline-block;
    width: 23px;
    height: 23px;
    border-radius: 50%;
    border: 2px solid $perfect-white;
    transition: background-color .15s ease-in-out;
  }
}
