.dev-workers {
  background: $grey-dawn;
  padding: 77px 0;
  margin-top: 63px;
  margin-bottom: 172px;
  @include tablet {
    padding: 115px 0;
  }
  @include laptop {
    padding: 150px 0;
  }
  &__inner {
    @include tablet {
      @include row-flex();
      flex-direction: column;
      display: flex;
    }
    @include laptop {
      flex-direction: row;
    }
  }
  &__card {
    @include size-lg(5);
    background: $violet-sky-b3;
    box-shadow: $lofty;
    display: flex;
    flex-direction: column;
    padding: 0 24px 47px;
    margin: 0 -24px;
    @include tablet {
      @include col();
      @include u-size(7, 8, 24px);
      border-radius: 24px;
      flex-direction: row-reverse;
      align-items: center;
      justify-content: space-between;
      padding: 50px 0 50px 64px;
    }
    & > div {
      max-width: 284px;
    }
    p {
      text-align: left;
      margin-bottom: 24px;
    }
    &:first-child {
      margin-bottom: 98px;
      .dev-workers__img {
        margin-top: -40px;
        @include tablet {
          margin-bottom: -50px;
          margin-right: -50px;
        }
      }
      @include tablet {
        margin-bottom: 40px;
      }
      @include laptop {
        margin-bottom: 0;
      }
    }
    &:last-child {
      @include laptop {
        @include shift-lg-left(1);
      }
      .dev-workers__img {
        margin-top: -80px;
        @include tablet {
          margin-top: -60px;
          margin-bottom: -60px;
          margin-right: -70px;
        }
      }
    }
  }
  &__img {
    text-align: center;
    @include tablet {
      img {
        width: 340px;
      }
    }
  }
}
