.hero {
  margin-top: -120px;
  overflow: hidden;
  @include tablet {
    margin-top: -170px;
  }
  &__inner {
    @include row-flex();
    flex-direction: column;
    @include laptop {
      flex-direction: row-reverse;
      align-items: flex-end;
    }
  }
  &__img {
    @include col();
    @include size-lg(6);
    width: calc(100% - 2rem);
    display: flex;
    justify-content: center;
    margin-bottom: 60px;
    @include tablet {
      margin-bottom: 72px;
    }
  }
  &__desc {
    @include col();
    @include tablet {
      @include u-size(6, 8, 24px);
    }
    @include size-lg(5);
    @include shift-md-left(1);
  }
  img {
    margin-left: 50px;
    @include tablet {
      margin-left: -50px;
    }
  }
  &__btns {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 56px;
    @include tablet  {
      flex-direction: row;
      margin-top: 72px;
    }
    button {
      &:first-child {
        margin-bottom: 36px;
        @include tablet {
          margin-bottom: 0;
          margin-right: 36px;
        }
      }
    }
  }
}
